.mob-menu{
    margin: 0 auto;
    font-size: 18px;
    line-height: 140%;
    width: 100%;
    & p {
        cursor: pointer;
        position: relative;
        margin: 15.5px 16px;
        padding-right: 134px;
        font-weight: bold;
        font-size: 18px;
        line-height: 110%;
        color: $grey-2;
        &:hover {
            color: $primary-purple;
        }
        &::after {
            position: absolute;
            content: "";
            background: url("../img/arrow_right.svg");
            background-repeat: no-repeat;
            background-position: center center;
            width: 24px;
            height: 24px;
            display: inline-block;
            top: -1px;
            right: 47px;
        }
    }
    &__list {
        display: none;
        position: relative;
        z-index: 2;
        margin: 0;
        list-style: none;
        background: $white;
        box-shadow: 0px 26px 26px $black-transparency;
    }
    &__item {
        padding: 10px;
        & a {
            color: $grey-2;
            &:hover {
                color: $primary-purple;
            }
        }

    }
}

@media (max-width: 767px){
    .mob-menu {
        display: block;
    }
}